<template>
  <div id="swagger-page" class="swagger-page lc-custom-scroll">
  </div>
</template>

<script>
import SwaggerPage from './SwaggerPage.hook'
const hook = new SwaggerPage();
hook.setBackendNamespace("Pages/Editor").setElementName("SwaggerPage");

export default {
  name: 'SwaggerPage',
  beforeCreate() { hook.hook(this); hook.addEventHandlers();},
  beforeDestroy(){ hook.clearToolBar();hook.unsubscribeGlobalEvents();}, 
  mounted(){ hook.initToolbar(); hook.initSwagger();  },
  methods: { },
  props: { },
  computed: hook.getters("openedDocument"),
  data() {
      return { }
  }
}

</script>

<style scoped lang="scss">

.swagger-page {

  width: 100%;
  height: 100%;
  background-color:rgb(255, 255, 255);
  overflow-y: auto;
}


</style>
