import {AtomicDesign} from '../../../../Frontend/Core/AtomicDesign';
import {getTopToolbarButtons,saveDocument} from '../../../../Frontend/Helpers';
import { SwaggerUIBundle } from 'swagger-ui-dist';
import Strings from '../../../UiResources/strings';
import {EVENTS} from '../../../UiResources/events';
require('swagger-ui-dist/swagger-ui.css');

export default class SwaggerPage extends AtomicDesign {
    
    resources = {};

    addEventHandlers(){
        this.onGlobalEvent(EVENTS.TOOLBAR.REFRESH, this.refresh);
        this.onGlobalEvent(EVENTS.TOOLBAR.DOWNLOAD_SWAGGER_DOC, this.downloadSwaggerDoc);
        this.onGlobalEvent(EVENTS.TOOLBAR.DOWNLOAD_POSTMAN_COLLECTION, this.downloadPostmanCollection);
        this.onGlobalEvent(EVENTS.TOOLBAR.CLIENT_CODE, this.generateClientCode);
    }

    downloadSwaggerDoc() {
        const filename = `${this.vue.openedDocument.editables['0'].properties['0'].title}-swagger.json`;
        const text = JSON.stringify(this.resources.swaggerDoc,null,"\t");
        this.downloadFileFromString(filename,text);
    }

    downloadFileFromString (filename,text){
        var element = document.createElement('a');
        element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
        element.setAttribute('download', filename);
      
        element.style.display = 'none';
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
    }

    async downloadPostmanCollection(){
        const docId = this.vue.openedDocument.id;
        const {result,error} = await this.backend(Strings.PROGRESS_INDICATOR.GENERATE_POSTMAN_COLLECTION).getPostmanCollection(docId);
        const showError = error || !result;

        if (!showError){
            const filename = `${this.vue.openedDocument.editables['0'].properties['0'].title}-postman.json`;
            const text = JSON.stringify(result);
            this.downloadFileFromString(filename,text)
        }else {
            this.msgbox("Error downloading postman collection",this.MSGBOX_TYPE.OK, "Error loading swagger documentation")
        }    
    }

    generateClientCode(){
        alert ("client code");
    }
    
    initToolbar(){
        const toolbarButtons = getTopToolbarButtons("SAVE","FULLSCREEN","REFRESH","DOWNLOAD_SWAGGER_DOC","DOWNLOAD_POSTMAN_COLLECTION");
        this.actions().setTopToolbar(toolbarButtons);
    }

    async refresh(){
        const swaggerDoc = await this.getSwaggerDoc();
        this.loadSwaggerUi(swaggerDoc);
    }

    async getSwaggerDoc(){
        await saveDocument(this);
        const docId = this.vue.openedDocument.id;
        const {result,error} = await this.backend(Strings.PROGRESS_INDICATOR.GENERATE_SWAGGER_DOC).getOpenApiDocument(docId);
        if (!error){
            this.resources.swaggerDoc = result;
            return result;
        }else {
            this.resources.swaggerDoc = null;
            this.msgbox("Error loading swagger documentation",this.MSGBOX_TYPE.OK, "Error loading swagger documentation")
        }        
    }

    saveDocument(){
        return new Promise((resolve)=>{
            this.emitGlobalEvent(EVENTS.TOOLBAR.SAVE, {
                callback:function(){resolve();},
                hideNotification: true
            });
        });
    }
    
    async initSwagger (){
        const swaggerDoc = await this.getSwaggerDoc();
        this.loadSwaggerUi(swaggerDoc);
    }

    loadSwaggerUi(spec){
        this.resources.swaggerUi = SwaggerUIBundle({
            spec,
            dom_id: '#swagger-page',
            presets: [
              SwaggerUIBundle.presets.apis,
            ],
            layout: "BaseLayout"
          })
    }
}